.action-btn{justify-content:center;align-items:center;position:relative;flex:1 1;margin:10px;padding:5px;min-width:100px;background-color:#fff;border-radius:5px;border:3px solid #666;cursor:pointer;overflow:hidden}.action-btn.preview{background-color:#d6fdd6;border:3px solid #00ff00}.action-btn.program{background-color:#fdd6d6;border:3px solid #ff0000}.action-btn img{flex:1 1;margin-bottom:30px}.action-name{position:absolute;bottom:15px;font-size:10pt;font-weight:bold;z-index:10;padding:0 5px}

#setupArea{padding:15px;flex-direction:column}#setupArea .formGroup{flex:1 1;padding-bottom:5px}#setupArea .formGroup label{padding:5px 15px;color:#fff;display:block;width:100px}#setupArea .formGroup input[type="text"],#setupArea .formGroup input[type="password"]{flex:1 1}#setupArea .formGroup input[type="radio"]{margin-right:10px}#setupArea .formGroup .optButton{align-items:center;color:#fff;padding:5px 0;padding-right:15px}

*{box-sizing:content-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}html,body,#root{background:#00001e;display:flex;width:100%;height:100%}div{display:flex}.container{flex:1 1;flex-direction:column}.status-line{align-items:center;background:#000;color:#f0f0f0;font-size:8pt;padding:5px 15px 5px 15px}.action-line{flex:1 1;flex-wrap:wrap;flex-direction:row;padding:5px}.action-line:last-child{flex:1 1;flex-direction:row}.btn-setup{border:1px solid #fff;font-weight:bold;font-size:10px;padding:5px 15px}.error-message{flex-direction:column}.error-message div{background:red;color:white;display:block;padding:10px 15px 10px 10px;margin-bottom:5px}.error-message button{padding:15px 5px}.info-message{flex-direction:column}.info-message div{background:#1d1deb;color:white;display:block;padding:10px 15px 10px 10px;margin-bottom:5px}

